import Localbase from "localbase";
import App from "./script.js";

class TransactionsList {
  dataBase = new Localbase("Wydatki");
  transactions;
  last;
  constructor() {
    this.last = document.querySelector(".last");
    this.last.addEventListener("click", () => this.getData());
  }
  getData() {
    this.dataBase
      .collection("expances")
      .get()
      .then((transactions) => {
        this.transactions = transactions;
        console.log(this.transactions);
        // x.map((el) =>
        //   console.log(
        //     `Kwota : ${el.value} Categoria: ${el.category} Data: ${el.date[0]}/${el.date[1]}/${el.date[2]} `
        //   )
        // );
      });
  }
}

export default transactionsList = new TransactionsList();
